@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* 
font-family: 'Poppins', sans-serif;
font-family: 'Roboto', sans-serif; */

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}

h1,h2,h3,h4,h5,h6{
  font-family: 'Poppins', sans-serif;
}

.img_splash{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  object-fit: cover;
}
.splash-screen {
  width: 100%; 
  height: 100%; 
  overflow: hidden;
}

.dashboard{
    display: flex;
    background-color: #F7F7F7;
    
}

.left__sidebar__dashboard{
    padding-top: 40px;
    flex: 0.2 1;
    position: fixed;
    box-shadow: 0px 5px 22px -13px grey;
    height: 100vh;
    width: 200px;
    background-color: white;
    
}

.main__body__dashboard{
    flex: 1 1;
    margin-left: 200px;
    padding-right: 20px;
    padding-left: 20px;
    
}

.right__sidebar__dashboard{
    flex: 0.2 1;
    
    
}

@media screen and (max-width: 768px) {
    .left__sidebar__dashboard{
       display: none;
       flex: 0 1;
      
        
    }
    .main__body__dashboard{
        flex: 1 1;
        margin-left: 0px;
        margin-right: 0px;
    }
    .right__sidebar__dashboard{
        flex: 1 1;
        
        
    }
    
}
.button-91 {
    color: #fff;
    padding: 10px 15px;
    background-color: #386ed2;
    background-image: radial-gradient(93% 87% at 87% 89%, rgba(0, 0, 0, 0.23) 0%, transparent 86.18%), radial-gradient(66% 66% at 26% 20%, rgba(255, 255, 255, 0.55) 0%, rgba(255, 255, 255, 0) 69.79%, rgba(255, 255, 255, 0) 100%);
    box-shadow: inset -3px -3px 9px rgba(255, 255, 255, 0.25), inset 0px 3px 9px rgba(255, 255, 255, 0.3), inset 0px 1px 1px rgba(255, 255, 255, 0.6), inset 0px -8px 36px rgba(0, 0, 0, 0.3), inset 0px 1px 5px rgba(255, 255, 255, 0.6), 2px 19px 31px rgba(0, 0, 0, 0.2);
    border-radius: 14px;
    font-weight: bold;
    font-size: 16px;
    border: 0;
    width: 90px;
  
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
  
    cursor: pointer;
  }

  .appointment {
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    margin: auto;
    margin-top: 50px;
    max-width: 90%px;
    padding: 30px;
  }
  
  .label {
    display: block;
    margin: auto;
    margin-top: 20px;
    font-size: 20px;
    color: #333;
    text-shadow: 1px 1px 2px #ccc;
    border-bottom: 1px dashed #ccc;
    padding-bottom: 5px;
  
  }
  
  .span{
    font-size: 20px;
    font-weight: bold;
    color: #333;
    transition: all 0.7s ease-in-out;
  }
  
  .span:hover{
    color: #000000;
    font-size: 25px;
    cursor: pointer;
  }
